<template>
  <div class="cate">
      <ul class="left">
        <li @click="n = index" :class="index == n?'select':''" v-for="(item,index) in namelist" :key="item.id">{{item.catename}}</li>
      </ul>



      <ul class="right">
        <li @click="$router.push('/catelist?fid='+item.id)" v-for="item in list[this.n].children" :key="item.id">
          <img :src="$pre + item.img" alt="">
          <h3>{{item.catename}}</h3>
        </li>
      </ul>


  </div>
</template>

<script>
import {reqGetcate,reqGetgoods,reqGetcatetree} from "../../utils/http"
export default {
  methods:{

  },
  data() {
    return{
      namelist:[],
      goodslist:[],
      list:[
        {children:""}
      ],
      n:0
    }
  },
  mounted() {
    reqGetcate().then(res=>{
      this.namelist = res.data.list
    }),
    reqGetcatetree().then(res=>{
      this.list = res.data.list
      console.log(this.list[0].children)
    })
  }

}
</script>

<style scoped lang="stylus">
@import '../../stylus/index.styl';
.cate{
  display flex
}
.left{
  li{
    height .7rem
    width 1.7rem
    border-left 5px solid $select-color
    line-height .7rem
    text-align center
  }
  .select{
    color $primary
    border-left 5px solid $font-color3
  }
}
.right{
  flex 1
  
  overflow hidden
  li{
    float left
    border 1px solid #9f6f9f
    width 2.5rem
    height 2.5rem
    margin-top 0.2rem
    border-radius 0.2rem
    overflow hidden
    position relative
    margin-left .1rem
    h3{
      position absolute
      bottom 0
      left 0
      background #9f5f9f
      width 100%
      text-align center
      line-height $h1
    }
    img {
      width 100%
      height 100%
    }
  }
}

</style>


